import { FormEvent, useMemo, useState } from "react";

type Status = "idle" | "loading" | "success" | "error";

// Remove a trailing slash so we do not end up with double slashes when building URLs.
const API_BASE_URL = (process.env.REACT_APP_API_BASE_URL ?? "").replace(/\/$/, "");

const Main = () => {
    const [username, setUsername] = useState("");
    const [status, setStatus] = useState<Status>("idle");
    const [message, setMessage] = useState<string>("");

    const endpointHint = useMemo(() => {
        const target = API_BASE_URL || "current origin";
        return `${target}/examiner/login`;
    }, []);

    const canSubmit = username.trim().length > 0 && status !== "loading";

    const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const trimmedName = username.trim();
        if (!trimmedName) {
            setStatus("error");
            setMessage("Please enter your name.");
            return;
        }

        setStatus("loading");
        setMessage("");

        try {
            const params = new URLSearchParams({ username: trimmedName });
            const url = `${API_BASE_URL}/examiner/login?${params.toString()}`;

            const response = await fetch(url, {
                method: "GET"
            });

            const body = await response.json().catch(() => ({}));

            if (!response.ok) {
                throw new Error(body.message ?? "Login failed");
            }

            setStatus("success");
            setMessage(body.message ?? "Login allowed");
        } catch (error) {
            const fallback = error instanceof Error ? error.message : "Login failed";
            setStatus("error");
            setMessage(fallback);
        }
    };

    return (
        <div className="page">
            <div className="card">
                <header>
                    <h1>Examiner Login</h1>
                    <p>Enter your name to access the evaluation platform.</p>
                </header>

                <form className="login-form" onSubmit={handleSubmit}>
                    <label className="label" htmlFor="username">
                        Name <span className="hint">Required</span>
                    </label>
                    <input
                        id="username"
                        className="input"
                        type="text"
                        autoComplete="name"
                        placeholder="e.g. Jamie Doe"
                        value={username}
                        onChange={(event) => setUsername(event.target.value)}
                    />

                    <button className="submit-btn" type="submit" disabled={!canSubmit}>
                        {status === "loading" ? "Signing in..." : "Sign in"}
                    </button>
                </form>

                {status === "success" && <div className="status success">{message}</div>}
                {status === "error" && <div className="status error">{message}</div>}

                <div className="info">
                    <div><strong>Login window:</strong> 8:00am12:00pm Eastern (backend enforces this).</div>
                    <div><strong>Endpoint:</strong> {endpointHint}</div>
                    <div className="hint">Set REACT_APP_API_BASE_URL to point at your backend if it is on a different origin.</div>
                </div>
            </div>
        </div>
    );
};

export default Main;
